import React from 'react';
import classnames from 'classnames';
import styles from './index.less';

interface IProp {
  className?: string;
  onClick?: () => void;
  children: React.ReactNode;
};

const Button = ({
  className,
  onClick,
  children,
}: IProp) => {

  return (
    <div className={classnames(styles.button, className)} onClick={onClick}>
      {children}
    </div>
  );
};

export default Button;
