import React, { useRef, useEffect, useState } from 'react';
import classnames from 'classnames';
import { useMusicStore } from '@/stores';
import styles from './index.less';
import { readLrcBlob } from '@/module/utils/tool';

interface IProp {
  className?: string;
  size?: 'large' | 'middle';
};

const SongLrc = ({ className, size= 'middle' }: IProp) => {
  const songList = useMusicStore((state) => state.songList);
  const currentSongIndex = useMusicStore((state) => state.currentIndex);
  const lyricsContainerRef: any = useRef(null);
  const listRef: any = useRef(null);
  const [initLoading, setInitLoading] = useState(true);
  const currentTime = useMusicStore((state) => state.currentTime);
  const [currentLrcIndex, setCurrentLrcIndex] = useState(0);
  const [currentLrcs, setCurrentLrcs] = useState<any[]>([]);

  useEffect(() => {
    setInitLoading(false);
  }, []);

  useEffect(() => {
    if (currentSongIndex !== -1 && songList[currentSongIndex]) {
      setCurrentLrcIndex(0);
      readLrcBlob(songList[currentSongIndex]?.lrc, (lrcEntries: any[]) => {
        setCurrentLrcs(lrcEntries);
      });
    }
  }, [currentSongIndex]);

  const findCurrentLyricLine = (time: number, lrcLines: any[]) => {
    for (let i = 0; i < lrcLines.length; i++) {
      if (lrcLines[i].time > time) {
        return i - 1 || 0;
      }
    }
    return lrcLines.length - 1;
  };

  // 处理歌词行的滚动定位
  const scrollLyricIntoView = (_currentLrcIndex: number) => {
    const lyricsContainer = lyricsContainerRef.current;
    if (lyricsContainer) {
      const lineElement = lyricsContainer.children[_currentLrcIndex];
      const containerRect = lyricsContainer.getBoundingClientRect();
      const lineElementRect = lineElement.getBoundingClientRect();
      const offsetTop = lineElementRect.top - containerRect.top;
      const scrollPosition = offsetTop - (containerRect.height - lineElementRect.height) / 2;
      // 滚动到指定位置
      listRef.current.scroll({
        top: scrollPosition,
        behavior: 'smooth'
      });
    }
  };

  useEffect(() => {
    const _currentLrcIndex = findCurrentLyricLine(currentTime, currentLrcs);
    if (currentLrcIndex !== _currentLrcIndex) {
      setCurrentLrcIndex(_currentLrcIndex);
      scrollLyricIntoView(_currentLrcIndex);
    }
  }, [currentTime]);

  return (
    <div
      className={classnames(styles.lrcPanel, !initLoading && styles.show)} ref={listRef}
    >
      {currentLrcs.length > 0 ? (
        <div className={classnames(styles.list, styles[size])} ref={lyricsContainerRef}>
          {currentLrcs.map((i, index) => <p key={index} className={classnames(currentLrcIndex === index && styles.on)}>{i.lyrics}</p>)}
        </div>
      ) : (
        <div className={styles.empty}>{currentSongIndex === -1 ? '' : '歌词缺失'}</div>
      )}
    </div>
  );
};

export default SongLrc;
