import React, { useRef, useEffect, useState } from 'react';
import classnames from 'classnames';
import { Carousel } from 'antd';
import { useMusicStore } from '@/stores';
import styles from './index.less';

interface IProp {
  className?: string;
};

/* 切换 */
const Cover = ({ className }: IProp) => {
  const songList = useMusicStore((state) => state.songList);
  const currentSongIndex = useMusicStore((state) => state.currentIndex);
  const coverCarouselRef: any = useRef(null);
  const [initLoading, setInitLoading] = useState(true);

  useEffect(() => {
    coverCarouselRef.current.goTo(currentSongIndex);
  }, [currentSongIndex]);

  useEffect(() => {
    setInitLoading(false);
  }, []);

  return (
    <Carousel
      dots={false}
      ref={coverCarouselRef}
      className={classnames(styles.cover, !initLoading && styles.show)}
      effect="fade"
    >
      {songList.map((item: any, index: any) => (
        <div key={index} className={styles.coverPanel}>
          <div style={{ backgroundImage: `url(${item.pic})` }} />
        </div>
      ))}
    </Carousel>
  );
};

export default Cover;
