import React, { useRef } from 'react';
import classnames from 'classnames';
import { InboxOutlined } from '@ant-design/icons';
import { useMusicStore } from '@/stores';
import Button from '../Button';
import { getCharCodes } from '@/module/utils/tool';
import styles from './index.less';

interface IProp {
  className?: string;
};

/* 设置 */
const InboxBtn = ({ className }: IProp) => {
  const inputRef = useRef(null);
  const updateMusicList = useMusicStore((state) => state.updateMusicList);

  // 选择本地音乐文件夹
  const handleDirectorySelect = (event: any) => {
    const files = event.target.files;
    const dateMap: any = {};

    let totalAudios = 0;
    let loadedAudios = 0;

    files.forEach((entry: any) => {
      if (entry.type.startsWith('audio/')) { totalAudios++; }
    });

    files.forEach((entry: any) => {
      const entryName = entry.name.toLowerCase();
      if (
        entryName.endsWith('.lrc') ||
        entry.type.startsWith('image/') ||
        entry.type.startsWith('audio/')
      ) {
        const fileName = entryName.replace(/\.[^.]+$/, '');
        const fileKey = getCharCodes(fileName);
        if (!dateMap[fileKey]) {
          const values = fileName.split(' - ');
          dateMap[fileKey] = {
            author: values.pop().trim().split(' _ '),
            title: values.join('').trim(),
          };
        }
        if (entryName.endsWith('.lrc')) {
          dateMap[fileKey].lrc = entry;
        }
        if (entry.type.startsWith('image/')) {
          dateMap[fileKey].pic = URL.createObjectURL(entry);
        }
        if (entry.type.startsWith('audio/')) {
          const url = URL.createObjectURL(entry);
          dateMap[fileKey].url = url;
          dateMap[fileKey].size = entry.size;
          dateMap[fileKey].lastModified = entry.lastModified;
          dateMap[fileKey].webkitRelativePath = entry.webkitRelativePath;
          const audio = new Audio(url);
          audio.addEventListener('loadedmetadata', function () {
            dateMap[fileKey].duration = audio.duration;
            loadedAudios++; // 增加已加载的音频数量
            // 检查是否所有音频都已加载完成
            if (loadedAudios === totalAudios) {
              updateMusicList(dateMap);
            }
          });
          audio.load();
        }
      }
    });
  };

  const handleInputClick = () => {
    if (inputRef?.current) {
      (inputRef?.current as any).click();
    }
  };

  return (
    <>
      <Button className={classnames(styles.inboxBtn, className)} onClick={handleInputClick}>
        <InboxOutlined />
        <span className={styles.title}>加载音乐</span>
      </Button>
      <input
        type="file"
        ref={inputRef}
        webkitdirectory="true"
        onChange={handleDirectorySelect}
        style={{ display: 'none' }}
      />
    </>
  );
};

export default InboxBtn;
