import React from 'react';
import { Space } from 'antd';
import classnames from 'classnames';
import CloudDownloadBtn from '@/module/components/CloudDownloadBtn';
import InboxBtn from '@/module/components/InboxBtn';
import SongList from '@/module/components/SongList';
import SongCover from '@/module/components/SongCover';
import SongLrc from '@/module/components/SongLrc';
import SongPlayer from '@/module/components/SongPlayer';
import Copyright from '@/components/Copyright';
import Cover from '@/module/components/Cover';
import { useMusicStore } from '@/stores';
import styles from './index.less';

const DefalutPanel = ({ showroom = false }) => {
  const onlyMode = useMusicStore((state) => state.onlyMode);
  const currentSongIndex = useMusicStore((state) => state.currentIndex);
  return (
    <div className={styles.container}>
      <div className={classnames(styles.backdrop, currentSongIndex === -1 && styles.emptyBackdrop)}>
        <Cover />
      </div>
      <div className={classnames(styles.panel, showroom && styles.showroom)}>
        {onlyMode === 'normal' ? (
          <div className={styles.main}>
            <div className={styles.left}>
              <Space className={styles.toolbar}>
                <InboxBtn />
                <CloudDownloadBtn />
              </Space>
              <div className={styles.songList}>
                <SongList />
              </div>
            </div>
            <div className={styles.right}>
              <div className={styles.songInfo}>
                <div className={styles.songCover}>
                  <SongCover />
                </div>
                <div className={styles.songLrc}>
                  <SongLrc />
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div className={styles.main}>
            <div className={styles.lrcPanel}>
              <SongLrc size='large' />
            </div>
          </div>
        )}
        <div className={styles.footer}>
          <SongPlayer />
          <div className={styles.copyright}><Copyright /></div>
        </div>
      </div>
    </div>
  );
};

export default DefalutPanel;
