import React from 'react';
import styles from './index.less';

function Copyright() {
  const date = new Date();
  const currentYear = date.getFullYear();
  const createYear = (process as any).createYear;
  const copyrightYear = (createYear && createYear !== currentYear) ? `${createYear}-${currentYear}` : currentYear;
  return (
    <div className={styles.copyright}>
      <div>「简单好用本地音乐播放器一键导入」</div>
      <div>{`©${copyrightYear} Yves`}</div>
      <div>{`Version: ${process.version}`}</div>
    </div>
  );
}

export default Copyright;
