import React, { useState, useEffect } from 'react';
import { Avatar } from 'antd';
import { useMusicStore } from '@/stores';
import { LogoIcon } from '@/components/Logo/Icon';
import styles from './index.less';

const MiniPanel = () => {
  const songList = useMusicStore((state) => state.songList);
  const currentSongIndex = useMusicStore((state) => state.currentIndex);
  const isPlaying = useMusicStore((state) => state.isPlaying);
  const [currentSong, setCurrentSong] = useState<any>(null);

  useEffect(() => {
    setCurrentSong(songList[currentSongIndex]);
  }, [currentSongIndex]);

  return (
    <div className={styles.container}>
      {/* <div className={styles.bgCover}>
      </div> */}
      <div className={styles.panel}>
        <div className={styles.left}>
          <Avatar className={styles.icon} icon={<LogoIcon />} size={44} />
        </div>
        <div className={styles.right}>
          {!isPlaying ? (
            <>
              <div className={styles.title}>
                本地音乐
              </div>
              <div className={styles.info}>
                点击播放
              </div>
            </>
          ) : (
            <>
              <div className={styles.title}>
                {currentSong?.title || ''}
              </div>
              <div className={styles.info}>
                {currentSong?.author.join(' / ') || '佚名'}
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default MiniPanel;
