/* eslint-disable no-unused-expressions */
import { StateCreator } from 'zustand';
import { ISong } from '@/module/utils/tool';

interface ITheme {
  type: string;
  title: string;
  audioSrc: string;
  imageSrc: string;
}

export interface MusicSlice {
  browserTitle: string;
  updateMusicList: (data: any) => void;
  songList: any[];
  songMap: any;
  currentIndex: number;
  gotoSong: (index: number) => void;
  audio: HTMLAudioElement;
  // initAudio: () => void;
  playAudio: () => void;
  pauseAudio: () => void;
  isPlaying: boolean;
  currentTime: number;
  updateCurrentTime: (currentTime: number) => void;
  currentVolume: number;
  updateCurrentVolume: (currentVolume: number) => void;
  orderMode: 'list' | 'random' | 'one',
  updateOrderMode: (orderMode: 'list' | 'random' | 'one') => void,
  onlyMode: 'normal' | 'lrc',
  updateOnlyMode: (onlyMode: 'normal' | 'lrc') => void,
};

export const createMusicSlice: StateCreator<MusicSlice> = (set) => ({
  browserTitle: window.document.title,
  songList: [],
  songMap: {},
  currentIndex: -1,
  updateMusicList: (dateMap) => {
    console.log('dateMap', dateMap);
    const musicMap: { [key: string]: 1 } = {};
    const musicList: ISong[] = [];
    Object.keys(dateMap).map(key => {
      const item = dateMap[key];
      if (item.url) {
        musicList.push(item);
        musicMap[key] = 1;
      }
    });
    return (set(state => {
      state.audio.src = musicList[0].url;
      return {
        songList: musicList,
        songMap: musicMap,
        currentIndex: 0,
        currentTime: 0,
        isPlaying: false,
      };
    }));
  },
  gotoSong: (index) => (set(state => {
    state.audio.src = state.songList[index].url;
    state.isPlaying && state.audio.play();
    return {
      currentIndex: index,
      currentTime: 0,
    };
  })),
  audio: new Audio(),
  isPlaying: false,
  // initAudio: () => (set(state => {
  //   return state;
  // })),
  playAudio: () => (set(state => {
    state.audio.paused && state.audio.play();
    return {
      isPlaying: true,
    };
  })),
  pauseAudio: () => (set(state => {
    !state.audio.paused && state.audio.pause();
    return {
      isPlaying: false,
    };
  })),
  currentTime: 0,
  updateCurrentTime: (currentTime: number) => (set({ currentTime })),
  currentVolume: 50,
  updateCurrentVolume: (currentVolume: number) => (set({ currentVolume })),
  orderMode: 'list',
  updateOrderMode: (orderMode) => (set({ orderMode })),
  onlyMode: 'normal',
  updateOnlyMode: (onlyMode) => (set({ onlyMode })),
});
