import React, { useRef, useEffect, useState } from 'react';
import classnames from 'classnames';
import { useMusicStore } from '@/stores';
import styles from './index.less';

interface IProp {
  className?: string;
};

const SongCover = ({ className }: IProp) => {
  const songList = useMusicStore((state) => state.songList);
  const currentSongIndex = useMusicStore((state) => state.currentIndex);
  const listRef: any = useRef(null);
  const [initLoading, setInitLoading] = useState(true);
  const [currentSong, setCurrentSong] = useState<any>(null);

  useEffect(() => {
    setInitLoading(false);
  }, []);

  useEffect(() => {
    setCurrentSong(songList[currentSongIndex]);
  }, [currentSongIndex]);

  console.log('[SongCover] currentSong', currentSong);

  return (
    <div
      ref={listRef}
      className={classnames(styles.coverPanel, !initLoading && styles.show)}
    >
      <div className={styles.infoCover} style={currentSong?.pic ? { backgroundImage: `url(${currentSong?.pic })` } : { backgroundColor: 'transparent' }} />
      <div className={styles.infoTitle}>{currentSong?.title ? `歌曲名：${currentSong?.title}` : ''}</div>
      <div className={styles.infoAuthor}>{currentSong?.author ? `歌手：${(currentSong?.author || ['佚名']).join(' / ')}` : ''}</div>
    </div>
  );
};

export default SongCover;
