import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import { Modal } from 'antd';
import { IProps } from './type';
import styles from './index.less';

// 外部visible拥有绝对控制权
export default function Popup(props: IProps): any {
  // 弹窗面板
  let { panel } = props;
  const {
    children, // 弹窗包裹容器
    width = 1024,
    height = 640,
    visible: _visible,
    onCancel,
    needMaximize = false,
    needRetract = null,
    // microAppKey,
    onRetract,
    bgCoverStyles,
    link,
    needTrack,
    reverse,
    resize = false,
  } = props;
  if (!panel) {
    return children;
  }
  // 控制弹窗展示
  const [visible, setVisible] = useState(_visible || false);
  // 控制弹窗销毁
  const [isActive, setIsActive] = useState(_visible || false);
  // 控制弹窗最大化
  const [isMaximize, setIsMaximize] = useState(_visible || needMaximize);

  useEffect(() => {
    if (_visible !== undefined) {
      setVisible(_visible);
      if (_visible) {
        setIsActive(true);
      }
    }
  }, [_visible]);
  // 打开弹窗
  const onOpen = () => {
    setIsActive(true);
    setVisible(true);
  };
  // 关闭弹窗
  const onClose = () => {
    setVisible(false);
    if (onCancel) {
      onCancel();
    }
  };
  // 完全关闭后的回调
  const onAfterClose = () => setIsActive(false);
  const onLink = () => {
    window.open(link);
  };

  // 渲染弹窗容器包含的孩子节点
  const renderChildren = () => {
    if (children) {
      return React.cloneElement(children as any, {
        onClick: () => {
          onOpen();
        }
      });
    }
    return undefined;
  };

  // 修改弹窗尺寸
  const onUpdateWindow = (type: 'maximize' | 'reduction') => {
    setIsMaximize(type === 'maximize');
  };

  // 渲染弹窗组件
  const renderModal = () => {
    return (
      <Modal
        open={_visible !== undefined ? _visible : visible}
        onCancel={onClose}
        afterClose={onAfterClose}
        destroyOnClose
        centered
        wrapClassName={classnames(styles.popupModel, isMaximize && styles.maxWindow)}
        width={width}
        title={null}
        footer={null}
        closable={false}
        maskClosable={false}
        styles={{ mask: { backdropFilter: 'blur(2px)' } }}
      >
        <div
          className={styles.viewResizeContainer}
          style={resize ? {} : {
            paddingBottom: isMaximize ? 0 : `${height / width * 100}%`,
            height: isMaximize ? '100%' : '0%',
          }}
        >
          <div
            className={styles.viewResizePanel}
            style={{
              backgroundColor: '#fff',
              position: resize ? 'relative' : 'absolute',
              ...bgCoverStyles,
            }}
          >
            <div className={classnames(styles.dialogPanel)}>
              {panel && panel({
                onClose,
              })}
            </div>
            <div className={classnames(styles.dialogTool, reverse && styles.reverse)}>
              {needMaximize !== null && (
                <div
                  className={classnames(styles.btn, styles.green)}
                  onClick={() => onUpdateWindow(isMaximize ? 'reduction' : 'maximize')}
                >
                  {isMaximize ? <i className={styles.iconCompressOut} /> : <i className={styles.iconExpandOut} />}
                </div>
              )}
              {needRetract !== null && (
                <div className={classnames(styles.btn, styles.yellow)} onClick={onRetract || onClose}>
                  <i className={styles.iconRetract} />
                </div>
              )}
              {link && (
                <div className={classnames(styles.btn, styles.blue)} onClick={onLink}>
                  <i className={styles.iconLink} />
                </div>
              )}
              <div className={classnames(styles.btn, styles.red)} onClick={onClose}>
                <i className={styles.iconClose} />
              </div>
            </div>
          </div>
        </div>
      </Modal>
    );
  };

  return (
    <>
      {renderChildren()}
      {isActive && renderModal()}
    </>
  );
}
