import React, { useRef, useEffect, useState } from 'react';
import classnames from 'classnames';
import { useMusicStore } from '@/stores';
import styles from './index.less';
import { secondToTime } from '@/module/utils/tool';

interface IProp {
  className?: string;
};

/* 列表 */
const SongList = ({ className }: IProp) => {
  const songList = useMusicStore((state) => state.songList);
  const currentSongIndex = useMusicStore((state) => state.currentIndex);
  const gotoSong = useMusicStore((state) => state.gotoSong);
  const listRef: any = useRef(null);
  const [initLoading, setInitLoading] = useState(true);
  console.log('songList', songList);

  useEffect(() => {
    setInitLoading(false);
  }, []);

  const handleClick = (index: number) => {
    gotoSong(index);
  };

  return (
    <div
      ref={listRef}
      className={classnames(styles.listPanel, !initLoading && styles.show)}
    >
      <div
        className={classnames(styles.listItem)}
      >
        <div className={styles.num}></div>
        <div className={styles.title}>歌曲</div>
        <div className={styles.author}>歌手</div>
        <div className={styles.duration}>时长</div>
      </div>
      {songList.map((item: any, index: any) => (
        <div
          key={index}
          className={classnames(styles.listItem, index === currentSongIndex && styles.highlight, styles.pointer)}
          onClick={() => handleClick(index)}
        >
          <div className={styles.num}>{(index > 998 ? 998 : index) + 1}</div>
          <div className={styles.title}>{item.title}</div>
          <div className={styles.author}>{item.author.join(' / ') || '佚名'}</div>
          <div className={styles.duration}>{secondToTime(item.duration)}</div>
        </div>
      ))}
    </div>
  );
};

export default SongList;
