import React, { useState, useEffect, useRef } from 'react';
import classnames from 'classnames';
import { Button, Input, Space } from 'antd';
import { useMusicStore } from '@/stores';
import styles from './index.less';
import { formattedTitle, getCharCodes, downloadSong } from '@/module/utils/tool';
import * as musicController from '@/module/controller';

interface IProp {
  className?: string;
};

const CloudDownloadPanel = ({ className }: IProp) => {
  const pauseAudio = useMusicStore((state) => state.pauseAudio);
  const songMap = useMusicStore((state) => state.songMap);
  useEffect(() => { pauseAudio(); }, []);

  const [resultList, setResultList] = useState<any[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const handleSearch = async (value: string) => {
    if (loading) { return false; }
    setLoading(true);
    const url = `https://api.i-meto.com/meting/api?server=netease&type=playlist&id=${value}`;
    const result = await musicController.getMusicList(url);
    setResultList((result?.data || []).map((song: any, index: any) => ({ ...song, index })));
    setLoading(false);
  };
  const audioRef = useRef<any>(null);
  const [currentSong, setCurrentSong] = useState<any>(null); // 当前播放的音乐
  const [isPlaying, setIsPlaying] = useState(false);

  const handlePlaySong = (song: any) => {
    if (currentSong?.index === song.index) {
      // 停止播发
      setIsPlaying(false);
      setTimeout(() => {
        if (audioRef?.current) {
          audioRef?.current.pause();
        }
      }, 100);
    } else {
      // 播放音乐的函数
      setCurrentSong(song);
      setIsPlaying(true);
      setTimeout(() => {
        if (audioRef?.current) {
          audioRef?.current.pause();
          audioRef?.current.play();
        }
      }, 100);
    }
  };

  const handleDownload = async (song: any) => {
    const fileName = formattedTitle(song);
    const fileKey = getCharCodes(fileName);
    if (songMap[fileKey]) {
      alert('歌曲已入库');
    } else {
      await downloadSong(song);
      alert('下载完成，请手动入库到指定的文件夹下');
    }
  };

  return (
    <div className={classnames(styles.cloudDownloadPanel, className)}>
      <div className={styles.searchPanel}>
        <Input.Search
          placeholder="输入网易云音乐歌单ID"
          allowClear
          enterButton="获取"
          size="large"
          onSearch={handleSearch}
          loading={loading}
        />
      </div>
      {resultList.length > 0 && (
        <div className={styles.resultPanel}>
          {resultList.map(song => (
            <div
              key={song.index}
              className={classnames(styles.resultItem)}
            >
              <div className={styles.info}>
                <div className={styles.title}>{song.title}</div>
                <div className={styles.author}>{song.author}</div>
              </div>
              <div className={styles.action}>
                <Space>
                  <Button ghost size='small' type="text" onClick={() => handlePlaySong(song)}>
                    {isPlaying && currentSong?.index === song.index ? '停止' : '试听'}
                  </Button>
                  <Button ghost size='small' onClick={() => handleDownload(song)}>下载</Button>
                </Space>
              </div>
            </div>
          ))}
        </div>
      )}
      <audio
        ref={audioRef}
        src={currentSong?.url || undefined}
        controls
        onPlay={() => setIsPlaying(true)}
        onPause={() => setIsPlaying(false)}
        style={{ display: 'none' }}
      />
    </div>
  );
};

export default CloudDownloadPanel;
