import React, { useRef, useEffect, useState } from 'react';
import classnames from 'classnames';
import { useMusicStore } from '@/stores';
import { Slider } from 'antd';
import {
  SkipIcon, PlayIcon, PauseIcon,
  VolumeDownIcon, VolumeUpIcon, VolumeOffIcon,
  OrderListIcon, OrderRandomIcon,
  LoopOneIcon, LoopNoneIcon, LoopAllIcon,
  NormalIcon, LrcIcon,
} from '../Icon';
import styles from './index.less';
import { getRandomIndex, secondToTime } from '@/module/utils/tool';

interface IProp {
  className?: string;
};

const SongPlayer = ({ className }: IProp) => {
  const songList = useMusicStore((state) => state.songList);
  const currentSongIndex = useMusicStore((state) => state.currentIndex);
  const audio = useMusicStore((state) => state.audio);
  const pauseAudio = useMusicStore((state) => state.pauseAudio);
  const playAudio = useMusicStore((state) => state.playAudio);
  const isPlaying = useMusicStore((state) => state.isPlaying);
  const listRef: any = useRef(null);
  const [initLoading, setInitLoading] = useState(true);
  const [currentSong, setCurrentSong] = useState<any>(null);
  const currentTime = useMusicStore((state) => state.currentTime);
  const updateCurrentTime = useMusicStore((state) => state.updateCurrentTime);
  const currentVolume = useMusicStore((state) => state.currentVolume);
  const updateCurrentVolume = useMusicStore((state) => state.updateCurrentVolume);

  const orderMode = useMusicStore((state) => state.orderMode);
  const updateOrderMode = useMusicStore((state) => state.updateOrderMode);
  const onlyMode = useMusicStore((state) => state.onlyMode);
  const updateOnlyMode = useMusicStore((state) => state.updateOnlyMode);
  const gotoSong = useMusicStore((state) => state.gotoSong);
  const [tempPrevVolume, setTempPrevVolume] = useState<number>(0);

  useEffect(() => {
    setInitLoading(false);
  }, []);

  useEffect(() => {
    setCurrentSong(songList[currentSongIndex]);
  }, [currentSongIndex]);

  const handleClickPlay = () => {
    if (isPlaying) { pauseAudio(); } else { playAudio(); }
  };

  const handleClickOrder = () => {
    const type: ('list' | 'random' | 'one')[] = ['list', 'random', 'one'];
    let index = type.indexOf(orderMode) + 1;
    if (index === type.length) { index = 0; }
    updateOrderMode(type[index]);
  };

  const handleClickOnly = () => {
    updateOnlyMode(onlyMode === 'normal' ? 'lrc' : 'normal');
  };

  const handleClickGoto = (type: 'prev' | 'next' = 'prev') => {
    let _index = 0;
    if (orderMode === 'list' || orderMode === 'one') {
      _index = currentSongIndex + (type === 'next' ? 1 : -1);
      if (_index < 0) { _index = songList.length - 1; }
      if (_index > songList.length - 1) { _index = 0; }
    } else if (orderMode === 'random') {
      _index = getRandomIndex(songList.length);
    }
    gotoSong(_index);
  };

  audio.ontimeupdate = () => {
    updateCurrentTime(audio.currentTime);
  };

  audio.onended = () => {
    if (isPlaying) {
      if (orderMode !== 'one') {
        handleClickGoto('next');
      } else {
        gotoSong(currentSongIndex);
      }
    }
  };

  const handleChangeSlider = (newValue: number) => {
    audio.currentTime = newValue * currentSong.duration / 100;
  };

  const handleSetVolume = (v: number) => {
    updateCurrentVolume(v);
  };

  const handleClickVolume = () => {
    console.log('_tempPrevVolume', tempPrevVolume, currentVolume);
    if (currentVolume !== 0) {
      setTempPrevVolume(currentVolume);
      updateCurrentVolume(0);
    } else {
      updateCurrentVolume(tempPrevVolume);
    }
  };

  useEffect(() => {
    audio.volume = currentVolume / 100;
    audio.muted = currentVolume === 0;
  }, [currentVolume]);

  return (
    <div
      ref={listRef}
      className={classnames(styles.playerPanel, !initLoading && styles.show, currentSongIndex === -1 && styles.disabled)}
    >
      <div className={styles.btnPrev} onClick={() => handleClickGoto('prev')}>
        <SkipIcon />
      </div>
      <div className={styles.btnPlay} onClick={handleClickPlay}>
        {isPlaying ? <PauseIcon /> : <PlayIcon />}
      </div>
      <div className={styles.btnNext} onClick={() => handleClickGoto('next')}>
        <SkipIcon />
      </div>
      <div className={styles.playerMusic}>
        {currentSong && (
          <div className={styles.playerInfo}>
            <div className={styles.playerTitle}>{currentSong?.title || ''}</div>
            <div className={styles.playerTime}>{currentSong ? `${secondToTime(currentTime)} / ${secondToTime(currentSong?.duration)}` : ''}</div>
          </div>
        )}
        <Slider
          className={styles.playerProgress}
          tooltip={{ open: false }}
          step={0.01}
          value={currentSong?.duration ? currentTime / currentSong?.duration * 100 : 0}
          onChange={handleChangeSlider}
        />
      </div>
      <div className={styles.order} onClick={handleClickOrder}>
        {orderMode === 'list' && <LoopAllIcon />}
        {orderMode === 'random' && <OrderRandomIcon />}
        {orderMode === 'one' && <LoopOneIcon />}
      </div>
      {/* <div className={styles.loop}>
        <LoopOneIcon />
      </div> */}
      <div className={styles.only} onClick={handleClickOnly}>
        {onlyMode === 'normal' ? <NormalIcon /> : <LrcIcon />}
      </div>
      <div className={styles.volumeBar}>
        <div className={styles.volume} onClick={handleClickVolume}>
          {currentVolume === 0 && <VolumeOffIcon />}
          {currentVolume === 100 && <VolumeUpIcon />}
          {currentVolume !== 0 && currentVolume !== 100 && <VolumeDownIcon />}
        </div>
        <Slider
          className={styles.volumeProgress}
          tooltip={{ open: false }}
          step={1}
          min={0}
          max={100}
          value={currentVolume}
          onChange={handleSetVolume}
        />
      </div>
    </div>
  );
};

export default SongPlayer;
