// 格式化时间
export const formatTime = (seconds: number) => {
  const minutes = Math.floor(seconds / 60);
  const remainingSeconds = seconds % 60;
  return `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;
};

export const getRandomIndex = (num: number) => {
  return Math.floor(Math.random() * num);
};

export const secondToTime = (second: number) => {
  const add0 = (num: number) => (num < 10 ? '0' + num : '' + num);
  const hour = Math.floor(second / 3600);
  const min = Math.floor((second - hour * 3600) / 60);
  const sec = Math.floor(second - hour * 3600 - min * 60);
  return (hour > 0 ? [hour, min, sec] : [min, sec]).map(add0).join(':');
};

// 读取Blob数据并解析LRC
export const readLrcBlob = (blob: any, cb: any) => {
  const reader = new FileReader();
  if (blob) {
    reader.onload = (e: any) => {
      const lrcText = e.target.result;
      const lrcEntries = parseLrcText(lrcText);
      cb(lrcEntries);
    };
    reader.readAsText(blob);
  } else {
    cb([]);
  }
};

// 解析LRC文本
export const parseLrcText = (lrcText: string) => {
  const lines = lrcText.split('\n');
  const entries = lines.map(line => {
    const [timeStr, lyrics] = line.replace('[', '').split(']');
    const time = convertTimeToSeconds(timeStr);
    return { time, lyrics: (lyrics || '').trim() };
  }).filter(line => !isNaN(line.time) && line.lyrics);
  return entries.sort((a, b) => a.time - b.time);
};

// 转换时间字符串为秒
export const convertTimeToSeconds = (timeStr: any) => {
  const [min, sec] = timeStr.split(':').map(Number);
  return min * 60 + sec;
};

export interface ISong {
  title: string;
  author: string;
  url: string;
  pic: string;
  lrc: string;
  size?: number;
  lastModified?: number;
  webkitRelativePath?: string;
};

// 根据请求获取文件的资源
export const fetchFile = async (url: string) => {
  const response = await fetch(url);
  if (!response.ok) {
    alert(`下载失败：HTTP错误，状态码 ${response.status}`);
    return '';
  }
  const blob = await response.blob();
  return window.URL.createObjectURL(blob);
};

// 下载文件的函数
export const downloadFile = async ({ url, filename }: { url: string, filename: string }) => {
  const link = document.createElement('a');
  link.href = url;
  link.download = filename;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  window.URL.revokeObjectURL(url); // 释放对象URL
};

// 管理单个音乐的下载任务 内含有pic、lrc、mp3
export const downloadSong = async (song: ISong) => {
  const fileName = formattedTitle(song); // 格式化文件名

  const promises = [
    fetchFile(song.url),
    fetchFile(song.pic),
    fetchFile(song.lrc)
  ];

  Promise.all(promises).then((urls) => {
    const [audioUrl, picUrl, lrcUrl] = urls;
    const links = [
      { url: audioUrl, filename: `${fileName}.mp3` },
      { url: picUrl, filename: `${fileName}.jpg` },
      { url: lrcUrl, filename: `${fileName}.lrc` }
    ];

    links.forEach(({ url, filename }) => downloadFile({ url, filename }));

    // alert('下载开始，请在浏览器中选择保存位置。');
  });
};

// 获取对应的文件名称
export const formattedTitle = (song: ISong) => `${song.title} - ${song.author}`;

// 获取文本对应的charCode
export const getCharCodes = (str: string) => `${Array.from(str).map(char => char.charCodeAt(0)).join('')}`;
