export const getMusicList = async (url: string) => {
  const result = await fetch(url)
    .then(response => response.text())
    .then(text => JSON.parse(text))
    .then(result => ({ success: true, data: result, message: '' }))
    .catch(error => ({ success: false, data: null, message: error?.message || 'Failed to fetch' }));
  return result;
};
